"use client";

import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import {
  Scan,
  TrendingUp,
  PieChart,
  Shield,
  Smartphone,
  Brain,
  Receipt,
  BarChart3,
  Target,
  ChevronRight,
  Download,
  Star,
  User,
} from "lucide-react";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { supabase } from "@/lib/supabaseClient";

export default function Home() {
  const [authChecked, setAuthChecked] = useState(false);
  const router = useRouter();

  useEffect(() => {
    supabase.auth.getSession().then(({ data }) => {
      if (data.session) {
        router.push("/dashboard");
      } else {
        setAuthChecked(true);
      }
    });
  }, [router]);

  if (!authChecked) {
    return (
      <div className="min-h-screen flex items-center justify-center text-xl">
        Loading...
      </div>
    );
  }

  // Key features
  const features = [
    {
      icon: <Scan className="w-8 h-8 text-blue-600" />,
      title: "Receipt OCR",
      desc: "Scan receipts and extract expenses instantly with high accuracy.",
    },
    {
      icon: <PieChart className="w-8 h-8 text-green-600" />,
      title: "Visual Insights",
      desc: "Interactive charts and reports to visualize your spending habits.",
    },
    {
      icon: <TrendingUp className="w-8 h-8 text-yellow-600" />,
      title: "Budget Forecasting",
      desc: "AI-driven predictions to help you stay on track.",
    },
    {
      icon: <Shield className="w-8 h-8 text-purple-600" />,
      title: "Secure & Private",
      desc: "Your data is encrypted and protected.",
    },
    {
      icon: <Smartphone className="w-8 h-8 text-pink-600" />,
      title: "Mobile Responsive",
      desc: "Track your finances on any device, with just internet and data",
    },
  ];

  // How it works steps
  const steps = [
    {
      icon: <User className="w-6 h-6 text-blue-600" />,
      title: "Sign Up",
      desc: "Create your free Optifi account.",
    },
    {
      icon: <Receipt className="w-6 h-6 text-green-600" />,
      title: "Scan Receipts",
      desc: "Upload or snap a photo of your receipt.",
    },
    {
      icon: <BarChart3 className="w-6 h-6 text-yellow-600" />,
      title: "Track & Analyze",
      desc: "See your expenses and insights instantly.",
    },
    {
      icon: <Target className="w-6 h-6 text-purple-600" />,
      title: "Achieve Goals",
      desc: "Use forecasts to improve your financial health.",
    },
  ];

  // Screenshots/demo placeholders
  const screenshots = [
    {
      src: "https://placehold.co/400x250?text=Dashboard+Demo",
      alt: "Dashboard Screenshot",
    },
    {
      src: "https://placehold.co/400x250?text=Receipt+OCR+Demo",
      alt: "Receipt OCR Screenshot",
    },
    {
      src: "https://placehold.co/400x250?text=Spending+Chart+Demo",
      alt: "Spending Chart Screenshot",
    },
  ];

  // Testimonials (placeholder)
  const testimonials = [
    {
      name: "Alex D.",
      text: "Optifi made tracking my expenses effortless. The receipt scanner is a game changer!",
      rating: 5,
    },
    {
      name: "Jamie L.",
      text: "I love the insights and charts. Now I actually understand where my money goes.",
      rating: 5,
    },
  ];

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 via-white to-slate-100">
      {/* Hero Section */}
      <section className="relative px-4 pt-32 pb-24 text-center flex flex-col items-center justify-center">
        <div className="max-w-4xl mx-auto">
          <h1 className="optifi-title mb-6">Optifi</h1>
          <p className="text-xl md:text-2xl text-slate-600 max-w-2xl mx-auto leading-relaxed mb-10">
            Financial Tracking Through OCR and AI-Driven Budget Forecasting
          </p>
          <div className="flex flex-col sm:flex-row gap-4 justify-center mb-8">
            <Link href="/register">
              <Button size="lg" className="text-lg px-8 py-6">
                Get Started
              </Button>
            </Link>
            <Link href="/login">
              <Button size="lg" variant="outline" className="text-lg px-8 py-6">
                Login
              </Button>
            </Link>
          </div>
        </div>
      </section>

      {/* Key Features */}
      <section className="max-w-5xl mx-auto px-4 py-12">
        <h2 className="text-3xl font-bold text-center mb-8">Key Features</h2>
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-8">
          {features.map((f, i) => (
            <Card
              key={i}
              className="flex flex-col items-center text-center p-6 shadow-md"
            >
              <div className="mb-4">{f.icon}</div>
              <CardTitle className="mb-2">{f.title}</CardTitle>
              <CardDescription>{f.desc}</CardDescription>
            </Card>
          ))}
        </div>
      </section>

      {/* How It Works */}
      <section className="bg-slate-100 py-12 px-4">
        <h2 className="text-3xl font-bold text-center mb-8">How It Works</h2>
        <div className="flex flex-col md:flex-row justify-center items-center gap-8 max-w-4xl mx-auto">
          {steps.map((step, i) => (
            <div
              key={i}
              className="flex flex-col items-center text-center max-w-xs"
            >
              <div className="mb-2">{step.icon}</div>
              <div className="font-semibold text-lg mb-1">{step.title}</div>
              <div className="text-slate-500">{step.desc}</div>
              {i < steps.length - 1 && (
                <ChevronRight className="hidden md:block w-6 h-6 text-slate-400 mt-4" />
              )}
            </div>
          ))}
        </div>
      </section>

      {/* Screenshots / Demo */}
      <section className="max-w-5xl mx-auto px-4 py-12">
        <h2 className="text-3xl font-bold text-center mb-8">
          See Optifi in Action
        </h2>
        <div className="flex flex-col md:flex-row gap-8 justify-center items-center">
          {screenshots.map((shot, i) => (
            <img
              key={i}
              src={shot.src}
              alt={shot.alt}
              className="rounded-lg shadow-lg border border-slate-200 w-full max-w-xs object-cover"
            />
          ))}
        </div>
      </section>

      {/* Testimonials */}

      {/* Footer */}
      <footer className="bg-slate-900 text-slate-300 py-12 px-4">
        <div className="max-w-6xl mx-auto">
          <div className="grid md:grid-cols-4 gap-8 mb-8">
            <div>
              <h3 className="text-2xl font-bold text-white mb-4">Optifi</h3>
              <p className="text-slate-400 leading-relaxed">
                Smart financial tracking through OCR and AI-driven insights.
              </p>
            </div>

            <div>
              <h4 className="font-semibold text-white mb-4">Support</h4>
              <ul className="space-y-2">
                <li>
                  <Link
                    href="/contact"
                    className="hover:text-white transition-colors"
                  >
                    Contact
                  </Link>
                </li>
                <li>
                  <Link
                    href="/about"
                    className="hover:text-white transition-colors"
                  >
                    Help Center
                  </Link>
                </li>
              </ul>
            </div>
            <div>
              <h4 className="font-semibold text-white mb-4">Legal</h4>
              <ul className="space-y-2">
                <li>
                  <a href="#" className="hover:text-white transition-colors">
                    Privacy Policy
                  </a>
                </li>
                <li>
                  <a href="#" className="hover:text-white transition-colors">
                    Terms of Service
                  </a>
                </li>
              </ul>
            </div>
          </div>
          <div className="border-t border-slate-700 pt-8 text-center text-slate-400">
            <p>&copy; 2025 Optifi. All rights reserved.</p>
          </div>
        </div>
      </footer>
    </div>
  );
}
